import React,{useState} from  'react';
import Tweet from './Tweet';
import axios from 'axios';
import Nav from './Pages/Nav';
import About from './Pages/About';
import Shop from './Pages/Shop';
import {BrowserRouter as Router,Switch,Route} from 'react-router-dom';
function App(){
  // Data
  const [isRed,setRed] = useState(true);
  const [count,setCount] = useState(0);
  // Functions
  const increment = ()=>{
    axios.get('http://tibetanbusiness.com:8888/api/event/list/all')
    .then(response=>{
      console.log(response);
    })
    setCount(count+2);
    setRed(!isRed);
  }
  // Initiatilisiting data
  const [users] = useState([
    {name:"Riinzin",message:29},
    {name:"Wangmo",message:23},
    {name:"Menkyi",message:28},
  ])
  // JXS part
  return(
    <Router>
    <div className="container mx-auto">
      <Nav/>
      <Switch>
        <Route path="/" exact component={Home}/>
        <Route path="/about" component={About}/>
        <Route path="/shop" component={Shop}/>  
      </Switch>
      {users.map(user=>(
        <Tweet name={user.name} message={user.message}/>
      ))}
      <h1 className={isRed ?"text-blue-400":"text-gray-600"}>Change my Color</h1>
      <button onClick={increment} className="bg-red-500 p-2">Increment</button>
      <h1 className="text-yellow-500">{count}</h1>
    </div>
</Router>
  )
}

// Home
const Home=()=>(
  <div>
    <h1>Home Page</h1>
  </div>
);
export default App;